
import HomeRecommend from './components/Recommend'
import HomeWeekend from './components/Weekend'
import Chart from './components/Chart'
import axios from 'axios'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
name: 'Home',
components: {
HomeRecommend,
HomeWeekend,
Datetime,
Group,
Tab,
TabItem,
Divider,
Chart
},
data () {
return {
target: '',
recommendList: [],
techInfo: [],
info: [],
projectDate: '2019-08'
}
},
methods: {
getHomeInfo () {
axios.get('/api/home/index?', {
params: {
id: this.$route.query.id
}
}).then(this.getHomeInfoSucc)
},
onItemClick (index) {
switch (index) {
case 0:
this.recommendList = this.target.td
break
case 1:
this.recommendList = this.target.st
break
case 2:
this.recommendList = this.target.dt
break
case 3:
this.recommendList = this.target.pc
break
default:
this.recommendList = this.target.td
}
},
getProjectRecord () {
axios.get('/api/home/queryProject?', {
params: {
date: this.projectDate,
id: this.$route.query.id
}
}).then(this.getProjectRecordSucc)
},
onConfirm (val) {
console.log('on-confirm arg', val)
this.projectDate = val
this.getProjectRecord()
},
change (value) {
console.log('change', value)
},
getProjectRecordSucc (res) {
res = res.data
if ((res.code === 0) && res.data) {
const data = res.data
this.target = data.target
this.recommendList = this.target.td
this.projectDate = data.projectDate
}
},
getHomeInfoSucc (res) {
res = res.data
if ((res.code === 0) && res.data) {
const data = res.data
this.swiperList = data.swiperList
this.iconList = data.iconList
this.info = data.info
this.target = data.target
this.recommendList = this.target.td
this.techInfo = data.techInfo
this.projectDate = data.projectDate
}
}
},
mounted () {
this.getHomeInfo()
},
activated () {
this.getHomeInfo()
}
}



