import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import City from '@/pages/city/City'
import Detail from '@/pages/detail/Detail'
import Chart from '@/pages/home/components/Chart'

Vue.use(Router)

export default new Router({
  mode: 'history',
  meta: {
    title: '技术档案'
  },
  routes: [{
    path: '/',
    name: 'Home',
    meta: {
      title: '技术档案'
    },
    query: {
      id: 'id'
    },
    component: Home
  }, {
    path: '/Chart',
    name: 'Chart',
    component: Chart
  }, {
    path: '/city',
    name: 'City',
    component: City
  }, {
    path: '/detail/:id',
    name: 'Detail',
    component: Detail
  }],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



